.mircophone-container-nav {
  display: -webkit-flex;
  display: flex;
}

.microphone-icon-container-nav {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-image: linear-gradient(128deg, #ffffff, #647c88);
  margin-top: 5px;
  padding: 5px;
  position: relative;
  cursor: pointer;
}
.microphone-icon-container-nav.listening::before {
  content: "";
  width: 30px;
  height: 30px;
  background-color: #ffffff81;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform:translate(-50%, -50%) scale(1.4);
          transform:translate(-50%, -50%) scale(1.4);
  border-radius: 50%;
  -webkit-animation: listening infinite 1.5s;
          animation: listening infinite 1.5s;
}
@-webkit-keyframes listening{
  0%{
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
  }
  100%{
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale(1.4);
            transform: translate(-50%, -50%) scale(1.4);
  }
}
@keyframes listening{
  0%{
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
  }
  100%{
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale(1.4);
            transform: translate(-50%, -50%) scale(1.4);
  }
}
